import { parse, set, addMinutes } from 'date-fns'

const TIME = new Date().toLocaleString('en-US', { timeZone: 'America/Los_Angeles' })
const SERVER_TIME = parse(TIME, 'MM/dd/yyyy, h:mm:ss a', new Date())
const LOCAL_TIMEZONE = new Date().toString().split(' ')[5]
const GAME_TIMEZONE = 'GMT-0700'
const TIMEZONE_DIFF = 
  (parseInt(GAME_TIMEZONE.split('-')[1]) - parseInt(LOCAL_TIMEZONE.split('-')[1])) / 100

export const NEXT_RESET = {
  year: SERVER_TIME.getUTCFullYear(),
  month: SERVER_TIME.getUTCMonth(),
  date: SERVER_TIME.getDate(),
  hours: 12 + TIMEZONE_DIFF,
  minutes: 59,
  seconds: 0
}
export const DAILY = set(new Date(), NEXT_RESET)
export const SUPPLY_COUNTER = addMinutes(DAILY, 1)