import React, { useState, useEffect } from 'react';
import { format, formatDistanceToNow } from 'date-fns'
import { 
  IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonGrid, IonRow, IonCol,
  IonCard, IonCardHeader, IonCardTitle, IonCardSubtitle, IonLabel, IonToggle,
  IonList, IonItem, IonSegment, IonSegmentButton
} from '@ionic/react'

import CLOCK_MODE from '../lib/clockMode'
import { DAILY, SUPPLY_COUNTER } from '../lib/nextReset'

const DATE_LONG = 'EEEE, MMMM d, yyyy'

const Clocks: React.FC = () => {

  const TIME_AP = 'h:mm:ss a'
  const TIME_24 = 'H:mm:ss'

  const [ time, setTime ] = useState<Date>(new Date())
  const [ daily ] = useState<Date>(DAILY)
  const [ supplyCounter ] = useState<Date>(SUPPLY_COUNTER)
  const [ timeFormat, setTimeFormat ] = useState<string>(CLOCK_MODE == 'AM/PM' ? TIME_AP : TIME_24)

  useEffect(() => {
    setTimeout(() => setTime(new Date()), 1000)
    localStorage.setItem('clock_mode', timeFormat == TIME_AP ? 'AM/PM' : '24 Hour')
  }, [ time, timeFormat ])

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle>Clocks</IonTitle>
        </IonToolbar>
        <IonToolbar>
          <IonSegment value={ timeFormat } onIonChange={ e => setTimeFormat(String(e.detail.value)) } >
            <IonSegmentButton value={ TIME_AP }>AM/PM</IonSegmentButton>
            <IonSegmentButton value={ TIME_24 }>24 Hour</IonSegmentButton>
          </IonSegment>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        <IonGrid className="ion-no-padding">
          <IonRow className="ion-justify-content-center">
            <IonCol size-md="6">
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Local Time</IonCardSubtitle>
                  <IonCardTitle>
                    { format(time, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>{ format(time, timeFormat) }</IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Mission Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(daily, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>
                    { format(daily, timeFormat) }&nbsp;
                    ({ formatDistanceToNow(daily, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Supply Counter Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(supplyCounter, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>
                  { format(supplyCounter, timeFormat) }&nbsp;
                  ({ formatDistanceToNow(supplyCounter, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
              <IonCard>
                <IonCardHeader>
                  <IonCardSubtitle>Next Daily Login Bonus Reset</IonCardSubtitle>
                  <IonCardTitle>
                    { format(daily, DATE_LONG) }
                  </IonCardTitle>
                  <IonCardTitle>
                    { format(daily, timeFormat) }&nbsp;
                    ({ formatDistanceToNow(daily, { addSuffix: true }) })
                  </IonCardTitle>
                </IonCardHeader>
              </IonCard>
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};

export default Clocks;
